<style>
  .store-num {
    cursor: pointer;
    color: #2077FF;
  }

  .store-num:hover {
    color: #409EFF;
  }
</style>

<template>
  <div>
    <!-- 操作框 -->
    <div class="handel-search-wraper">
      <div class="handel-wraper">
        <el-button type="primary" @click="addPage" size="mini" icon="el-icon-plus">新建页面</el-button>
      </div>
      <hr class="s-slip">
      <div class="search-form-wraper">
        <el-form :model="searchContent" label-position="top">
          <el-form-item label="创建时间">
            <date-picker-range 
              :defaultRange="searchContent.defaultRange"
              format="yyyy-MM-dd"
              type="daterange"
              @datePickerRangeChange="datePickerRangeChange">
            </date-picker-range>
          </el-form-item>
          <region-city-store @StoreGroupChange="StoreGroupChange" :showGroup="false" :showStore="false" :defaultCityId="searchContent.cityId"></region-city-store>
          <el-form-item label="页面名称">
            <el-input class="search-input" v-model.trim="searchContent.pageName" placeholder="请输入页面名称" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
          </el-form-item>
          <el-form-item label="页面类型">
            <el-select class="search-input" v-model="searchContent.pageType" filterable placeholder="请选择页面类型" size="small" @change="doSearchHandel(true)">
              <el-option
                v-for="(item, index) in pageTypeOptions"
                :key="index"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <!-- <el-form-item label="状态">
            <el-select class="search-input" v-model="searchContent.useStatus" filterable placeholder="请选择页面类型" size="small" @change="doSearchHandel(true)">
              <el-option
                v-for="item in orderStatusOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item> -->

          <el-form-item>
            <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
    
    <!-- 搜索结果 -->
    <div class="table-wraper">
      <el-table
        class="fixed-height"
        v-loading="loading"
        :data="tableData"
        empty-text="没有查询到您想要的数据"
        style="width:100%;"
        size="small">
        <el-table-column min-width="70px" prop="id" label="页面ID"></el-table-column>
        <el-table-column min-width="120px" prop="pageName" label="页面名称"></el-table-column>
        
        <el-table-column min-width="120px" prop="pageTypeName" label="页面类型"></el-table-column>
        <el-table-column min-width="110px" prop="regionName" label="区域"></el-table-column>
        <el-table-column min-width="100px" label="应用门店数">
          <template slot-scope="scope" size="small">
            <router-link :to="scope.row.userStoreNumRedirectUrl" target="_blank">{{scope.row.userStoreNum || 0}}</router-link>
          </template>
        </el-table-column>
        <el-table-column min-width="100px" label="上架门店数">
          <template slot-scope="scope" size="small">
            <router-link :to="scope.row.userOnStoreNumRedirectUrl" target="_blank">{{scope.row.userOnStoreNum || 0}}</router-link>
          </template>
        </el-table-column>

        <el-table-column min-width="170px" prop="createTime" label="创建时间"></el-table-column>
        <el-table-column min-width="170px" prop="updateTime" label="修改时间"></el-table-column>
        <!-- <el-table-column min-width="100px" prop="useStatusStr" label="状态"></el-table-column> -->
        <el-table-column
          fixed="right"
          label="操作"
          width="140">
          <template slot-scope="scope" size="small">
            <div>
              <el-button @click="showDetail(scope.row)" type="text" size="mini" icon="el-icon-edit">编辑</el-button>
              <el-button @click="deleteItem(scope.row)" type="text" size="mini" icon="el-icon-delete">删除</el-button>
            </div>
            <div>
              <el-button @click="copyItem(scope.row)" type="text" size="mini" icon="el-icon-document">复制</el-button>
            </div>
            <!-- <div class="shareline" v-clipboard:copy="scope.row.copyUrl" v-clipboard:success="onCopySuc" v-clipboard:error="onCopyError">
               <el-button type="text" size="mini" icon="el-icon-document">复制链接</el-button>
            </div> -->
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页条 -->
      <el-pagination
        v-if="total>0"
        class="tc mt20"
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="pageNum"
        :page-sizes="[20, 50, 100, 150, 200]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>

    <el-dialog 
      :close-on-click-modal = "false"
      :close-on-press-escape = "false"
      title="选择模板" 
      :visible.sync="dialogFormVisible" 
      width='420px'>
      <el-form ref="form" label-width="90px">
        <el-form-item label="选择模板" required>
          <el-select class="search-input" v-model="tempId" filterable clearable placeholder="请选择页面类型" size="small" @change="changeSelectTemp">
            <el-option
              v-for="(item, index) in selectTempOptions"
              :key="index"
              :label="item.tempName"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="goNextHandel" size="mini" :disabled="pageType === '' || !pageType">下一步</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Storage from '@/utils/localStorage'
import * as utils from '@/utils/utils'
import DatePickerRange from '@/components/Common/DatePickerRange'
import RegionCityStore from '@/components/Common/StoreCommon/RegionCityStore';
const setPageMgtUrl = {
  // 获取模板
  findTemp: '/plateform/a/mall/pageTemp/findTemp',
  // 页面管理列表
  pageTempList: '/plateform/a/mall/pageTemp/pageTempList',
  // 模板删除
  delPage: '/plateform/a/mall/pageTemp/delPage'

}
  export default {
    data() {
      return {
        loading: false,
        pageNum: 1,
        pageSize: 20,
        total: 0,
        searchContent: {
          DIFFCITY: true,
          regionCode: Storage.get('regionCode') || '', // 区域ID
          cityId: '',
          pageName: '', // 页面名称
          pageType: 0, // 页面类型：1-首页；2-专题页
          useStatus: '', // 使用状态：1-上架；2-下架
          defaultRange: [], // 创建时间范围
          createTime: '', // 创建时间起
          updateTime: '' // 创建时间止
        },
        // 页面类型
        pageTypeOptions: [
          {
            label: '全部',
            value: 0
          },
          {
            label: '首页',
            value: 1
          },
          {
            label: '专题页',
            value: 2
          }
        ], 
        // 选择模板
        selectTempOptions: [],
        // 使用状态
        orderStatusOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '上架',
            value: 1
          },
          {
            label: '下架',
            value: 2
          }
        ],

        // 添加页面 第一步
        dialogFormVisible: false,
        // 选择页面类型 1 首页  2 模板
        pageType: '', 
        tempId: '', // 模板ID
        tableData: []

      }
    },
    created() {
      // 获取模板
      this.findTemp()
    },
    mounted() {
      let searchContentMsg = Storage.get("searchContent")
      if(this.$route.meta.keepAlive && searchContentMsg) {
        this.pageNum = searchContentMsg.pageNum,
        this.searchContent = searchContentMsg.searchContent

        Storage.remove('searchContent')
      }
      // 获取表格中的数据
      this.doSearchHandel()
    },
    components: {
      "date-picker-range": DatePickerRange,
      "region-city-store": RegionCityStore
    },
    methods: {
      StoreGroupChange(param) {
        this.searchContent.regionCode = param.regionCode
        this.searchContent.cityId = param.cityId
        this.doSearchHandel(true)
      },
      /**
       * [addPage 新建页面]
       */
      addPage() {
        if(this.selectTempOptions.length === 0) {
          this.findTemp()
        }
        this.pageType = ''
        this.dialogFormVisible = true
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      /**
       * [selectPageTopicList 获取选择专题的列表]
       * @return {[type]} [description]
       */
      findTemp() {
        this.$http.get(setPageMgtUrl.findTemp).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              this.selectTempOptions = [...res.data]
            }else {
              this.selectTempOptions = []
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(获取选择专题的列表):", e.message)
        })
      },
      /**
       * [doSearchHandel 获取订单列表内容]
       * @param  {[type]} _param [description]
       * @return {[type]}        [description]
       */
      doSearchHandel(_param) {
        if(_param === true) {
          this.pageNum = 1
        }
        this.loading = true
        this.$http.post(setPageMgtUrl.pageTempList, { ...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize }).then(res => {
          this.loading = false
          if(res.code === 0) {
            // 获取表格数据
            if(res.data) {
              if(res.data.result && res.data.result.length) {
                for(let item of res.data.result ) {
                  if(item.pageType === 1) {
                    // 首页
                    item.copyUrl = `pages/shopping/newindex?id=${item.id}`
                  }else if(item.pageType === 2) {
                    // 专题页
                    item.copyUrl = `pages/shopping/subject?id=${item.id}`
                  }
                  item.userStoreNumRedirectUrl = `/store_page?pageName=${item.pageName}&pageType=${item.pageType}`
                  item.userOnStoreNumRedirectUrl = `/store_page?pageName=${item.pageName}&pageType=${item.pageType}&useStatus=1`
                }
                this.tableData = res.data.result
              }else {
                this.tableData = []
              }

              this.total = res.data.total
            }else {
              this.total = 0
              this.tableData = []
            }
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [goNextHandel 下一步]
       * @return {[type]} [description]
       */
      goNextHandel() {
        if(!this.pageType || !this.tempId) {
          this.$message("请选择模板")
          return
        }
        this.$router.push("/set_page/add/" + this.pageType + '/' + this.tempId)
      },
      /**
       * [changeSelectTemp 当选择模板发生变化的时候触发]
       * @param  {[type]} _val [description]
       * @return {[type]}      [description]
       */
      changeSelectTemp(_val) {
        if(!_val) {
          return
        }
        let tempItemMesg = this.selectTempOptions.filter(item => {
          return item.id === _val
        })[0]
        this.tempId = tempItemMesg.id
        this.pageType = tempItemMesg.pageType
      },
      /**
       * [showDetail 编辑]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      showDetail(_row) {
        // 设置搜索条件
        let _paramMesg = {
          pageNum: this.pageNum,
          searchContent: this.searchContent
        }
        Storage.set("searchContent", _paramMesg)
        let pageType = _row.pageType
        let tempId = _row.templateId
        let pageId = _row.id
        let pageName = _row.pageName
        let useStatus = _row.useStatus
        let regionName = _row.regionName
        let regionCode = _row.regionCode
        this.$router.push(`/set_page/edit/${pageType}/${tempId}/${pageId}?regionCode=${regionCode}&regionName=${regionName}&pageName=${pageName}&useStatus=${useStatus}&actionType=edit`)
      },
      /**
       * [deleteItem 删除]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      deleteItem(_row) {
        let pageId = _row.id
        let pageType = _row.pageType
        let _params = {
          pageId: pageId,
          pageType: pageType,
          isDeleted: 1
        }
        this.$confirm('您确定要删除所选页面吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$http.post(setPageMgtUrl.delPage, _params).then(res => {
            if(res.code === 0) {
              this.$message.success("删除成功")
              this.doSearchHandel()
              return 
            }
            throw new Error(res.msg)
          }).catch(e => {
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },
      /**
       * [copyUrl 复制链接成功]
       * @return {[type]}      [description]
       */
      onCopySuc() {
        this.$message.success("链接已复制至剪切板，仅限微信公众号中使用")
      },
      /**
       * [onCopyError 复制链接失败]
       * @return {[type]} [description]
       */
      onCopyError() {
        this.$message.error("复制链接失败,请手动复制")
      },
      /**
       * @description: 复制操作
       * @param {type} 
       * @return: 
       */
      copyItem(_row) {
        // 设置搜索条件
        let _paramMesg = {
          pageNum: this.pageNum,
          searchContent: this.searchContent
        }
        Storage.set("searchContent", _paramMesg)
        let pageType = _row.pageType
        let tempId = _row.templateId
        let pageId = _row.id
        let pageName = _row.pageName
        let useStatus = _row.useStatus
        let regionName = _row.regionName
        let regionCode = _row.regionCode
        this.$router.push(`/set_page/edit/${pageType}/${tempId}/${pageId}?regionCode=${regionCode}&regionName=${regionName}&pageName=${pageName}&useStatus=${useStatus}&actionType=copy`)
      },
      /**
       * @description: 下单时间范围发生变化
       * @param {type} 
       * @return: 
       */
      datePickerRangeChange(result) {
        this.searchContent.defaultRange = result ? [...result] : result
        this.searchContent.createTime = result ? result[0] : ''
        this.searchContent.updateTime = result ? result[1] : ''
        this.doSearchHandel(true)
      }
    },
    /**
     * [beforeRouteLeave 页面离开]
     * @param  {[type]}   to   [description]
     * @param  {[type]}   from [description]
     * @param  {Function} next [description]
     * @return {[type]}        [description]
     */
    beforeRouteLeave (to, from, next){
      from.meta.keepAlive = false;
      next();
    }

  }
</script>


